/* Sitemap.
----------------------------------------------------------------------------- */

.sitemap {
    ul, 
    li {
        margin: 0;
        padding: 0;
        list-style: none;
        padding: 5px;
        background: transparent;
    }

    ul > ul{

        li {
            color: get-text-color($main-theme);
            background: $main-theme;
            padding: 10px;
            font-weight: bold;
            font-size: 1em; 
            line-height: 1;
            &.parent{
                cursor: pointer;
            }

            a {
                color: get-text-color($main-theme);
                width: 100%;
                display: block;
                height: 100%;
            }

            span {
                float: left;
            }

            i{
                transform: rotate(-90deg);
                transition: transform 1s;
                float: right;

                &.open{
                    transform: rotate(0);
                    transition: transform 1s;
                }
            }
        }

        & > ul li {
            color: get-text-color($main-theme-complementary);
            background: $main-theme-complementary;
            padding: 5px 10px;
            margin: 0;
            font-weight: normal;

            a {
                color: get-text-color($main-theme-complementary);
            }

            &:hover {
                background: $main-theme;
                color: get-text-color($main-theme);

                a {
                    color: get-text-color($main-theme);
                    text-decoration: none;
                }
            }
        }
    }
}

@each $type-name, $bg-color, $text-color, $highlight-color in $many-colors {

    .sitemap-#{""+$type-name} {

        ul > ul {

            li {
                color: get-text-color($bg-color);
                background: $bg-color; 

                a {
                    color: get-text-color($bg-color);
                }
            }

            & > ul li {
                color: get-text-color(lighten($bg-color, 25%));
                background: lighten($bg-color, 25%);

                a {
                    color: get-text-color(lighten($bg-color, 25%));
                }
            }
        }
    }
}